.modal-container {
  left: 50%;
  top: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  border: 1px solid #00b7c3;
  background-color: white;
  border-radius: 16px;
  height: auto;
  overflow: hidden;
  height: 50%;
  width: 50%;

  .modal-content {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    gap: 1em;
    width: auto;
    padding: 1em;
    .title {
      font-weight: 500;
      font-size: 1.3em;
      width: 100%;
      border-bottom: 1px solid #00b7c3;
      padding-bottom: 0.5em;
    }

    .item-box {
      display: flex;
      flex-direction: row;
      gap: 3em;
    }
    .submit-button {
      font-family: inherit;
      align-self: center;
      background: #00b7c3;
      color: white;
      border: none;
      padding: 0.8em;
      border-radius: 18px;
      width: 50%;
      font-size: 1em;
    }
  }

  .select-field {
    width: 100%;
    border: 1px solid #00b7c3;
    border-radius: 18px;
    padding: 0.7em;
    font-size: 1em;
    font-weight: 400;

    &:focus-visible {
      outline: none;
    }
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

@media (max-width: 768px) {
  .modal-container {
    min-width: 70%;
  }
}
